.navbar{
    display: flex;
    justify-content: space-between;
    padding: 25px 60px;
    font-size: 1.3vw;
    font-weight: 600;
    color: white;
    height: 80px;
    position: fixed;
    width: 100%;
    z-index: 100;
    background-color: rgb(11, 35, 51);
}  
.navlinks{
    display: flex;
    gap:3vw;
    cursor: pointer;
}
.navlink{
    text-decoration: none;
    color: white;
    transition: 0.3s;
}
.navlink:hover{
    color:rgb(0, 190, 156);
}