@import url('https://fonts.googleapis.com/css2?family=Jost:wght@200;300;400&display=swap');
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Jost;
}

body {
    background-color: rgb(11, 35, 51);
}
.sections-container{
    /* scroll-behavior: smooth; */
    overflow-y: auto;
    overscroll-behavior: contain;
    scroll-snap-type: y mandatory;
    width:100%;
    height:100vh;
}
::-webkit-scrollbar{
    display: none;
}
.section{
    width:100%;
    height: 100vh;
    scroll-snap-align: start;
    padding: 80px 10%;
}

h2{
    text-align: center;
    color:white;
}